@import "../_store-variables.less";
@import "../_store-mixins.less";


.fn-@{store-ns}-info{
	@bd-color: #d5d5d5;

	.info-wrap{
		background:#fff;
		border: 1*@rex solid @bd-color;
    	border-left: none;
    	border-right: none;

    	.item{
			line-height: 45*@rex;
		    font-size: 15*@rex;
		    overflow: hidden;
		    border-bottom: 1*@rex solid @bd-color;
		    margin-left:3%;

		    .item-right{
		    	padding-right:3%;
		    	font-size: 14*@rex;
		    }

		    i{
		    	font-size: 18*@rex;
		    	margin-right:8*@rex;
		    }

		    .i-moumou{
		    	color:#4d95c2;
		    }

		    .i-phone{
		    	color: #007aff;
		    }

		    &:last-child{
		    	border-bottom: 0;
		    }
    	}
	}
}