@import "../_store-variables.less";
@import "../_store-mixins.less";


.fn-@{store-ns}-score{

	.score-wrap{
		padding:3%;
		background:#fff;
		border: 1*@rex solid #D5D5D5;
    	border-left: none;
    	border-right: none;

    	.line{
    		line-height:28*@rex;
    		font-size: 15*@rex;
    		overflow: hidden;

    		.level{
    			float: right;
    			font-size: 14*@rex;
    		}

    		.up{
    			color: @ht-color;
    		}

    		.down{
    			color: #6bb53d;
    		}
    	}
	}
}