@import "../_store-variables.less";
@import "../_store-mixins.less";


.fn-@{store-ns}-collection{
	background: #fff;

	.collection-wrap{

		.item{
			border-bottom: 1*@rex solid #D5D5D5;
    		padding: 2% 0 2% 2%;
    		position: relative;
		}

		.pic{
			position: relative;

			&:before{
				content: "";
    			display: block;
    			width: 100%;
    			padding-top: 110%;
			}

			.img{
				.absolute(0,_,_,0);
				.size(100%);
				max-height: 100%;
			}
		}

    	.info{
    		padding-left: 3.2%;

			.title{
    			font-size: 15*@rex;
    			padding-bottom:5*@rex;
    		}

    		.evaluation{
    			font-size: 14*@rex;
    		}

    		.icon{
    			text-align: center;
    			font-size: 18*@rex;
    		}

    		.text{
    			font-size: 14*@rex;
    			text-align: center;
    		}
    	}

    	.active{
    		color: @ht-color;
    	}
	}

}