@import "../_store-variables.less";
@import "../_store-mixins.less";


.fn-@{store-ns}-card{


	.card-wrap{
		background:#fff;
		border: 1*@rex solid @bd-color;
    	border-left: none;
    	border-right: none;
    	position: relative;

    	 &:before{
    		content:'';
    		display:block;
    		width: 100%;
			padding-top: 12%;
		}

		.card-box{
			.absolute(0,_,_,0);
			.size(100%);
		}

    	.card{
    		display: block;
		    line-height: 38*@rex;
		    font-size: 15*@rex;
		    position: relative;
		    z-index: 2;
		    margin-left:3%;

		    i{
		    	padding-right:3%;

		    	&:before{
		    		content:"\e608";
		    	}
		    }
    	}

    	.qrcode{
			transform: translateY(-100%);
		    transition: all 0s;
		    z-index: -1;
		    position: relative;
		    opacity: 0;

		    .text{
		    	text-align: center;
			    font-size: 14*@rex;
			    color: #666;

			    span{
		    	    margin-top: -6%;
				    display: inline-block;
				    vertical-align: top;
			    }
		    }
    	}

    	.card-img-bg{
    		background-size: 50% auto;
    		background-repeat: no-repeat;
    		background-position: center center;

    		&:before{
    			padding-top:68%;
    		}
    	}

    	.card-img{
    		.absolute(50%,_,_,0);
    		width: 30%;
		    margin: -15% 0 0 35%;
    	}
	}
}

.@{container-class} .card-active{
	height: auto;

	.fn-store-card{

		.card-wrap{

			&:before{
	    		padding-top: 90%;
			}

			.card{
				border-bottom: 1*@rex solid @bd-color;

				i:before{
					content:"\e607";
				}
			}

			.qrcode{
				transform: translateY(0);
				transition: all .1s;
			    z-index: 1;
			    opacity: 1;
			}
		}
	}
}